import React from 'react'
import clsx from 'clsx'

import config, { NODES } from '../config/config'
import Connection from '../Connection'
import Settings from '../Settings'
import i18n from '../i18n'

import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'
import DialogActions from '@material-ui/core/DialogActions'
import Button from '@material-ui/core/Button'
import Paper from '@material-ui/core/Paper'
import Tabs from '@material-ui/core/Tabs'
import Tab from '@material-ui/core/Tab'

import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'

import TableBody from '@material-ui/core/TableBody'
import TableRow from '@material-ui/core/TableRow'
import TableCell from '@material-ui/core/TableCell'

import ExpansionPanel from '@material-ui/core/ExpansionPanel'
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails'
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary'
import Typography from '@material-ui/core/Typography'
import ExpandMoreIcon from '@material-ui/icons/ExpandMore'

import TextField from '@material-ui/core/TextField'
import FormControl from '@material-ui/core/FormControl'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import FormHelperText from '@material-ui/core/FormHelperText'
import Switch from '@material-ui/core/Switch'
import Select from '@material-ui/core/Select'
import Checkbox from '@material-ui/core/Checkbox'
import InputLabel from '@material-ui/core/InputLabel'
import MenuItem from '@material-ui/core/MenuItem'

import EnhancedTable from '../components/EnhancedTable'


/*
 * STYLES
 */
import { withStyles } from '@material-ui/core/styles'
const styles = theme => ({
	tabs: {
		marginBottom: '10px'
	},
	heading: {
		fontSize: theme.typography.pxToRem(15),
		flexBasis: '33.33%',
		flexShrink: 0,
	},
	secondaryHeading: {
		fontSize: theme.typography.pxToRem(15),
		color: theme.palette.text.secondary,
	},
	closeButton: {
		position: 'absolute',
		right: theme.spacing(1),
		top: theme.spacing(1),
		color: theme.palette.grey[500],
	},
	dialogContent: {
		backgroundColor: '#fafafa'
	},
	title: {
		paddingLeft: theme.spacing(2),
		paddingRight: theme.spacing(1),
		minHeight: '64px',
		display: 'flex',
		position: 'relative',
		alignItems: 'center'
	},
	input: {
		width: 300
	},
	textField: {
		marginLeft: theme.spacing(1),
		marginRight: theme.spacing(1)
	},
	rawEditor: {
		fontFamily: 'Roboto Mono,Consolas,Courier New,monospace !important'
	}
});


const headCells = [
  { id: 'name', disablePadding: true, label: i18n.t('Name') },
  { id: 'function', disablePadding: false, label: i18n.t('Function') },
  { id: 'states', disablePadding: false, label: i18n.t('States') },
  { id: 'jarvis', disablePadding: false, label: i18n.t('Jarvis') },
  { id: 'attributes', disablePadding: false, label: i18n.t('Attributes') },
];

class SettingsPage extends React.Component {
	
	constructor(props) {
		super(props);
		
		delete Settings.settings.iobroker;
		this.state = {
			tab: 0,
			panel: false,
			settings: Settings.settings || {}
		}
		
		this.rows = Object.keys(this.props.devices).map(deviceId => ({ id: deviceId, ...this.props.devices[deviceId] }));
	}
	
	/*
	 *
	 */
	handleTabChange = (event, tab) => this.setState({ tab });
	handlePanelChange = panel => (event, isExpanded) => this.setState({ panel: isExpanded ? panel : false });
	handleSwitchChange = key => event => {
		let userInput = (event && event.target && event.target.checked) || false;
		this.setState(prevState => ({ settings: { ...prevState.settings, [key]: userInput } }));
	}
	
	handleTextChange = key => event => {
		let userInput = (event && event.target && event.target.value) || '';
		
		if (key) {
			this.setState(prevState => ({ settings: { ...prevState.settings, [key]: userInput } }));
		}
		else {
			this.setState({ settings: JSON.parse(userInput) });
		}
	}
	
	/*
	 *
	 */
	save = () => {
		
		Settings.setSettings(this.state.settings);
		Connection.getConnection.setState(NODES.SETTINGS, JSON.stringify(Settings.settings))
			.then(() => window.location.reload())
			.catch(err => console.error(err));
	}
	
	/*
	 *
	 */
	renderSettingForSelect(key, setting) {
		const { classes } = this.props;
		
		setting.options = setting.options || {};
		const options = Object.keys(setting.options).map(option => <MenuItem key={'MenuItem-' + key + '-' + option} value={option}>{setting.options[option]}</MenuItem>);
		return (

<FormControl key={'FormControl-' + key} className={classes.formControl}>
	<InputLabel>{setting.label}</InputLabel>
	<Select
		key={'Select-' + key}
		className={classes.input}
		value={this.state.settings[key] || setting.default || ''}
		onChange={this.handleTextChange(key)}
		>
		
		{options}
	</Select>
	<FormHelperText>{setting.helperText || ''}</FormHelperText>
</FormControl>

		)
	}
	
	/*
	 *
	 */
	renderSettingForText(key, setting) {
		const { classes } = this.props;
		
		return (

<TextField
	key={'TextField-' + key}
	className={clsx(classes.input, classes.textField)}
	label={setting.label}
	placeholder={setting.default || ''}
	helperText={setting.helperText || ''}
	onChange={this.handleTextChange(key)}
	value={this.state.settings[key] || ''}
	/>

		)
	}
	
	/*
	 *
	 */
	renderSettingForSwitch(key, setting) {
		
		return (

<FormControlLabel
	key={'FormControlLabel-' + key}
	control={<Switch checked={this.state.settings[key] !== undefined ? this.state.settings[key] : (setting.default || false)} onChange={this.handleSwitchChange(key)} />}
	label={setting.label}
	/>

		)
	}
	
	/*
	 *
	 */
	renderSetting(key, setting) {
		
		switch(setting.type) {
			case 'text':
				return this.renderSettingForText(key, setting);
			
			case 'switch':
				return this.renderSettingForSwitch(key, setting);
			
			case 'select':
				return this.renderSettingForSelect(key, setting);
				
			default:
				return null;
		}
	}
	
	/*
	 *
	 */
	renderPanel(key, panel) {
		const { classes } = this.props;
		
		const settings = Object.keys(panel.settings).map(setting => this.renderSetting(setting, panel.settings[setting]));
		return (

<ExpansionPanel
	key={'ExpansionPanel-' + key}
	expanded={this.state.panel === key}
	onChange={this.handlePanelChange(key)}
	>
	
	<ExpansionPanelSummary
		expandIcon={<ExpandMoreIcon />}
		id={'panel-header-' + key}
		>
		
		<Typography className={classes.heading}>{panel.label}</Typography>
		{panel.description && <Typography className={classes.secondaryHeading}>{panel.description}</Typography>}
	</ExpansionPanelSummary>
	<ExpansionPanelDetails>
		{settings}
	</ExpansionPanelDetails>
</ExpansionPanel>

		)
	}
	
	renderTableForDevices = (props) => {
		const { columns, pageRows, emptyRows, selected, onClick } = props;
		const isSelected = id => selected.indexOf(id) !== -1;
		
		return (

<TableBody>
	{this.rows.map((device, index) => {
		const isItemSelected = isSelected(device.id);
		const labelId = `device-${index}`;
		
		return (
			<TableRow
				hover
				onClick={event => props.onClick(event, device.id)}
				role="checkbox"
				aria-checked={isItemSelected}
				tabIndex={-1}
				key={device.id}
				selected={isItemSelected}
				>
				
				<TableCell padding="checkbox"><Checkbox checked={isItemSelected} inputProps={{ 'aria-labelledby': labelId }} /></TableCell>
				<TableCell id={labelId} scope="row"><TextField InputProps={{ disableUnderline: true }} style={{ width: 400 }} value={device.name} /></TableCell>
				<TableCell id={labelId} scope="row"><TextField InputProps={{ disableUnderline: true }} style={{ width: 400 }} value={device.function} /></TableCell>
				
				<TableCell id={labelId} scope="row"><TextField /></TableCell>
				
				<TableCell id={labelId} scope="row"><TextField /></TableCell>
				
				<TableCell id={labelId} scope="row"><TextField /></TableCell>
				
			</TableRow>
		)
	})}
</TableBody>

		)
	}
	
	/*
	 *
	 */
	render() {
		const { classes } = this.props;
		
		const panels = Object.keys(config).map(panel => this.renderPanel(panel, config[panel]));
		return (

<React.Fragment>
	<DialogTitle disableTypography>
		<Typography variant="h6">Configuration</Typography>
		<IconButton aria-label="close" className={classes.closeButton} onClick={() => this.props.onClose()}>
			<CloseIcon />
		</IconButton>
	</DialogTitle>
	<DialogContent className={classes.dialogContent}>
		
		<Paper square>
			<Tabs
				className={classes.tabs}
				value={this.state.tab}
				indicatorColor="primary"
				textColor="primary"
				onChange={this.handleTabChange}
				>
				
				<Tab label="Devices" />
				<Tab label="Settings" />
				<Tab label="Layout" />
				<Tab label={["Settings & Layout", <br key='tab' />, "Raw Editor"]} />
			</Tabs>
		</Paper>
		<div hidden={this.state.tab !== 0}>
			<Paper className={classes.paper}>
				<EnhancedTable
					title="Devices"
					headCells={headCells}
					rows={this.rows}
					CustomTableBody={this.renderTableForDevices}
					/>
			</Paper>
			
		</div>
		<div hidden={this.state.tab !== 1} style={{ width: '100%' }}>
			<Paper className={classes.title}>
				<Typography variant="h6" id="tableTitle">
					{i18n.t('Settings')}
				</Typography>
			</Paper>
			{panels}
			
		</div>
		<div hidden={this.state.tab !== 2}>
			Layout
			
		</div>
		<div hidden={this.state.tab !== 3} style={{ width: '100%' }}>
			<Paper className={classes.paper}>
				<Typography className={classes.title} variant="h6" id="tableTitle">
					{i18n.t('Raw Editor')}
				</Typography>
				<TextField
					InputProps={{ className: classes.rawEditor }}
					key={'TextField-Raw'}
					multiline
					fullWidth
					onChange={this.handleTextChange(null)}
					value={JSON.stringify(this.state.settings, null, 3)}
					/>
			</Paper>
			
		</div>
		
	</DialogContent>
	<DialogActions>
		<Button autoFocus onClick={() => this.props.onClose()} color="secondary">
			{i18n.t('Cancel')}
		</Button>
		<Button onClick={this.save} color="primary">
			{i18n.t('Save')}
		</Button>
	</DialogActions>
</React.Fragment>

		)
	}
}

export default withStyles(styles)(SettingsPage);
