import React, { useState, useEffect } from 'react'
import clsx from 'clsx'
import color from 'color'

import Button from '@material-ui/core/Button'
import ButtonGroup from '@material-ui/core/ButtonGroup'
import ExpandLessIcon from '@material-ui/icons/ExpandLess'
import ExpandMoreIcon from '@material-ui/icons/ExpandMore'
import StopIcon from '@material-ui/icons/Stop';

import i18n from '../../i18n'
import defaults from '../defaults'
import getActivityType from './blind-manufacturers'

/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles';
const useStyles = makeStyles(theme => ({
	value: props => ({
		float: 'left',
		lineHeight: '32px',
		marginRight: '10px'
	}),
	borderLeftTransparent: {
		borderLeftColor: 'transparent !important'
	},
	borderLeftColor: {
		borderLeftColor: color(theme.palette.primary.main).alpha(0.5).rgb().toString() +  '!important'
	},
	borderRightTransparent: {
		borderRightColor: 'transparent !important'
	},
	borderRightColor: {
		borderRightColor: color(theme.palette.primary.main).alpha(0.5).rgb().toString() +  '!important'
	},
	borderRightColorSecondary: {
		borderRightColor: color(theme.palette.secondary.main).alpha(0.5).rgb().toString() +  '!important'
	}
}));


/*
 * i18n
 */
let translations = require('./blind.' + i18n.getLanguage()).default
i18n.setTranslation(i18n.getLanguage(), translations);


/*
 * ACTIONS
 *
 */
function BlindLevelAction(props) {
	const { device, state, stateKey } = props;
	const classes = useStyles();
	
	const onGoUp = () => {
		setLevel(100);
		setActivity(true);
		device.setDeviceState(stateKey, 100).catch(err => console.error(err));
	}
	
	const onGoDown = () => {
		setLevel(0);
		setActivity(true);
		device.setDeviceState(stateKey, 0).catch(err => console.error(err));
	}
	
	const onStop = () => {
		setActivity(false);
		device.setDeviceState('activity', true).catch(err => console.error(err));
	}
	
	// level
	const [level, setLevel] = useState(state.value);
	useEffect(() => setLevel(state.value), [state]);
	
	// state change
	const stateChange = (sKey, s, d) => {
		
		if (sKey === 'activity') {
			setActivity(s.val);
		}
	}
	
	// activity
	const [activity, setActivity] = useState(false);
	useEffect(() => {
		
		if (device.states.activity && device.states.activity.state) {
			device.states.activity.state = typeof device.states.activity.state === 'string' ? { 'node': device.states.activity.state } : device.states.activity.state;
			
			// request device state
			device.requestDeviceState('activity', true).catch(err => console.error(err));
			
			// listen for updates
			device.listen(stateChange)
		}
		
		// componentWillUnmount
		return () => device.unlisten(stateChange);
		
	}, [device, state]);
	
	return (

<React.Fragment>
	
	<div className={classes.value}>
		{level + state.unit}
	</div>
	
	<ButtonGroup size="small">
		<Button color="primary" className={clsx(activity !== null && (!activity ? classes.borderRightColor : classes.borderRightTransparent))} onClick={onGoUp}><ExpandLessIcon /></Button>
		{activity !== null && <Button color="secondary" className={clsx(!activity ? [ classes.borderLeftTransparent, classes.borderRightTransparent ] : classes.borderRightColorSecondary)} onClick={onStop} disabled={!activity}><StopIcon /></Button>}
		<Button color="primary" className={clsx(activity !== null && (!activity ? classes.borderLeftColor : classes.borderLeftTransparent))} onClick={onGoDown}><ExpandMoreIcon /></Button>
	</ButtonGroup>
</React.Fragment>

	);
}


export default {
	configurations: {
		level: {
			value: val => Math.round(val),
			icon: {
				'default': 'window-shutter-open',
				'>90': 'window-shutter-open',
				'<=90': 'window-shutter'
			}
		},
		level2: {
			value: val => Math.round(val),
			unit: ' %',
			icon: {
				'default': 'window-shutter-open',
				'>90': 'window-shutter-open',
				'<=90': 'window-shutter'
			}
		},
		activity: {
			val: (val, stateKey, device) => getActivityType(device.options.manufacturer, val),
			icon: {
				true: 'pan-vertical',
				false: 'dots-vertical',
			},
			state: {
				true: i18n.t('blind#activity#true'),
				false: i18n.t('blind#activity#false'),
			}
		}
	},
	components: {
		level2: defaults.components.LevelComponent
	},
	actions: {
		level: BlindLevelAction
	},
	styles: {
	}
}