/*
 * Code by MATERIAL-UI
 * @see https://material-ui.com/components/tables/#sorting-amp-selecting
 *
 */
import React from 'react'

import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import Checkbox from '@material-ui/core/Checkbox'


export default function EnhancedTable(props) {
  const { columns, pageRows, emptyRows, selected, onClick } = props;
  const isSelected = id => selected.indexOf(id) !== -1;

  return (

<TableBody>
  {pageRows.map((row, index) => {
	  const isItemSelected = isSelected(row.id);
	  const labelId = `enhanced-table-checkbox-${index}`;

	  return (
		<TableRow
		  hover
		  onClick={event => onClick(event, row.id)}
		  role="checkbox"
		  aria-checked={isItemSelected}
		  tabIndex={-1}
		  key={row.id}
		  selected={isItemSelected}
		>
		  <TableCell padding="checkbox">
			<Checkbox
			  checked={isItemSelected}
			  inputProps={{ 'aria-labelledby': labelId }}
			/>
		  </TableCell>
		  
		  {columns.map(column => (
			<TableCell key={'TableCell-' + index + '-' + column.id} id={labelId} scope="row" padding="none">{row[column.id] || ''}</TableCell>
		  ))}
		  
		</TableRow>
	  );
	})}
  {emptyRows > 0 && (
	<TableRow style={{ height: 53 * emptyRows }}>
	  <TableCell colSpan={6} />
	</TableRow>
  )}
</TableBody>

  );
}