export default function getActivityType(manufacturer, val) {
	
	// HomeMatic
	if (manufacturer === 'HomeMatic') {
		
		switch(val) {
			case 1: // UP
			case 2: // DOWN
				val = true;
				break;
				
			case 3: // STABLE
				val = false;
				break;
				
			default: // UNKNOWN
				val = null;
				break;
		}
	}
	
	// Shelly
	else if (manufacturer === 'Shelly') {
		
		switch(val) {
			case 'open': // MOVING
			case 'close': // MOVING
				val = true;
				break;
				
			case 'stop': // MOVING
				val = false;
				break;
				
			default: // UNKNOWN
				val = null;
				break;
		}
	}
	
	return val;
}